declare const JavaBridgeObj: any

export function getMp4url(url: string) {
  const DOMAIN = window.location.hostname
  if (DOMAIN.indexOf('cdn-') === -1) {
    return url.replace('cdn-', '')
  }
  return url
}

export function preloadVideo(source: string) {
  const URL = window.webkitURL || window.URL

  return new Promise((resolve, reject) => {
    const xhr = new XMLHttpRequest()
    xhr.open('GET', source, true)
    xhr.responseType = 'blob'

    xhr.onload = function () {
      if (xhr.status === 200 || xhr.status === 304) {
        const res = xhr.response
        if (/iphone|ipad|ipod/i.test(navigator.userAgent)) {
          const fileReader = new FileReader()
          fileReader.onloadend = function () {
            const resultStr = fileReader.result! as string
            resolve(resultStr)
          }
          fileReader.readAsDataURL(xhr.response)
        } else {
          resolve(URL.createObjectURL(res))
        }
      } else {
        reject(new Error(`http response invalid${xhr.status}`))
      }
    }

    xhr.send()
  })
}

export function getSource() {
  const entryFrom = getArgsFromUrlHash('entry_from') || 'banner'
  const map: any = {
    banner: '首页banner',
    room: '房间活动位',
    msg: '站内信',
    screen: '公屏提醒',
  }

  return map[entryFrom] || map.banner
}
export function delay(time: number) {
  return new Promise((r) => setTimeout(r, time))
}

export function formatNickName(name: string) {
  return name.replace(
    /[\u061C\u200E\u200F\u202A\u202B\u202C\u202D\u202E\u2066\u2067\u2068\u2069]/g,
    ''
  )
}

const domainMap: any = {
  'oss-pk-arab-zl.badambiz.com': 'oss-pk-arab-zl.badambiz.com',
  'oss-pk-arab.badambiz.com': 'oss-pk-arab.badambiz.com',
}

export function fixRankDomain(url: string) {
  if (url && url.startsWith('http')) {
    try {
      const u = new (window.URL || window.webkitURL)(url)
      const { host } = u
      const domain = domainMap[host]

      if (domain) {
        u.host = domain
      }

      return u.toString()
    } catch (err) {
      console.error(err)
      return url
    }
  }

  return url
}

export function updateDomain() {
  Object.keys(domainMap).forEach((key: string) => {
    const newUrl = fixDomain(`https://${key}/aaa`)
    const u = new (window.URL || window.webkitURL)(newUrl)
    const { host } = u

    domainMap[key] = host
  })
}

/// share 系统函数
export async function sysShare(text: string, link: string, sharedCallback: any) {
  if (navigator.share) {
    console.warn('sysShare', text, link, sharedCallback)
    sharedCallback && (await sharedCallback())
    try {
      await navigator.share({
        title: '',
        // text: `${link} ${text}`,
        text,
        url: link,
      })
      console.log('分享成功')
      //上报分享过
    } catch (err) {
      console.log('分享失败:', err)
    } finally {
      console.log('分享结束')
    }
  } else {
    // 不支持 Web Share API,可以降级使用其他分享方案
    console.log('当前环境不支持系统分享')
  }
}

// 屏蔽安卓
export const disableAndroid = () => {
  const ua = navigator.userAgent
  const isIpad = /(?:iPad)/.test(ua)
  if (!getPlatform().isIPhone && !isIpad) {
    showToast(_T('操作失败'), undefined, undefined, undefined, '50%')
    return true
  }
  return false
}

export const domainList = {
  'https://cdn1.facebookcdn.xyz/': false,
  'https://arab-pk.badambiz.com/': false,
}

export function isEnd(needTip = true) {
  const endTs = store.state.end * 1000
  if (getSysTs() >= endTs) {
    if (needTip) {
      showToast(_T('活动已结束'), undefined, undefined, undefined, '50%')
    }
    return true
  }
  return false
}

export function notStart(needTip = true) {
  const startTs = store.state.start * 1000
  if (getSysTs() < startTs) {
    if (needTip) {
      showToast(_T('活动未开始'), undefined, undefined, undefined, '50%')
    }
    return true
  }
  return false
}

export function preloadResource(source: string, type: string) {
  const URL = window.webkitURL || window.URL

  return new Promise((resolve, reject) => {
    const xhr = new XMLHttpRequest()
    xhr.open('GET', source, true)
    xhr.responseType = 'blob'

    xhr.onload = function () {
      if (xhr.status === 200 || xhr.status === 304) {
        const res = xhr.response
        if (/iphone|ipad|ipod/i.test(navigator.userAgent)) {
          const fileReader = new FileReader()
          fileReader.onloadend = function () {
            const resultStr = fileReader.result! as string
            const raw = atob(resultStr.slice(resultStr.indexOf(',') + 1))
            const buf = Array(raw.length)
            for (let d = 0; d < raw.length; d += 1) {
              buf[d] = raw.charCodeAt(d)
            }
            const arr = new Uint8Array(buf)
            const blob = new Blob([arr], {
              type,
            })
            resolve(URL.createObjectURL(blob))
          }
          fileReader.readAsDataURL(xhr.response)
        } else {
          resolve(URL.createObjectURL(res))
        }
      } else {
        reject(new Error(`http response invalid${xhr.status}`))
      }
    }

    xhr.send()
  })
}
