/* eslint-disable no-use-before-define */
import Vue from 'vue'
import '@badam/vue-tsx-support/enable-check'
import '@badam/vue-tsx-support/options/enable-nativeon'
import {
  calculateWindow,
  exec,
  getServTs,
  setLang,
  updateTsDelta,
  appType,
  DayJs,
  TimeType,
  textWithRTL,
  getArgsFromUrlHash,
} from '@badam/common-utils'
import lang from '../../lang/index.ksa.json'
import Index from './index.vue'
import '../../index.module.scss'
import { setDomain, traceOutside } from '@/api'
import { TraceType } from '@/api/interface'
import { firebaseManager } from '@/firebase'
// import { domainManager } from '@/new_modules/domain'

Vue.config.productionTip = false
Vue.mixin({
  methods: {
    textWithRTL,
  },
})

if (getArgsFromUrlHash('lan') !== 'zh') {
  setLang(lang)
} else {
  setLang({}, '', true)
}

document.documentElement.lang = process.env.VUE_APP_PROJ_NAME

exec(async () => {
  // @Td
  await setActDomain()
  calculateWindow(750)
  const time = await getServTs(appType.live)
  DayJs.setOffset(TimeType.Live)
  updateTsDelta(time)
  traceOutside(TraceType.拉新分享页)
  new Vue({
    render: (h) => h(Index),
  }).$mount('#app')
})

async function setActDomain() {
  try {
    firebaseManager.setup()
    const res: any = await firebaseManager.getValueFromConfig('ow_conf')
    const r = JSON.parse(res)
    const { act } = r
    setDomain(act[0])
  } catch (e) {
    console.error('setDomain error', e)
  }
}
