import { Analytics, getAnalytics, logEvent } from 'firebase/analytics'
import { FirebaseApp, FirebaseOptions, initializeApp } from 'firebase/app'
import { FirebasePerformance, getPerformance } from 'firebase/performance'
import { fetchAndActivate, getRemoteConfig, getValue, RemoteConfig } from 'firebase/remote-config'

export class FirebaseManager {
  LOG_TAG = 'firebase-manager'

  app: FirebaseApp | undefined

  analytics: Analytics | undefined

  remoteConfig: RemoteConfig | undefined

  firebaseConfig: FirebaseOptions | undefined

  perf: FirebasePerformance | undefined

  static instance: FirebaseManager

  static getInstance(): FirebaseManager {
    if (!FirebaseManager.instance) {
      FirebaseManager.instance = new FirebaseManager()
    }
    return FirebaseManager.instance
  }

  async setup() {
    this.firebaseConfig = {
      apiKey: 'AIzaSyBlR0QK--SjqMqihBd03ARBzfuZ3x1euzo',
      authDomain: 'badamoyundixim.firebaseapp.com',
      databaseURL: 'https://badamoyundixim.firebaseio.com',
      projectId: 'badamoyundixim',
      storageBucket: 'badamoyundixim.appspot.com',
      messagingSenderId: '532022495714',
      appId: '1:532022495714:web:2a3620b2f1f41d319fd1bb',
      measurementId: 'G-8L1XP304KR',
    }
    // Initialize Firebase
    this.app = initializeApp(this.firebaseConfig)
    this.analytics = getAnalytics(this.app)
    this.remoteConfig = getRemoteConfig(this.app)
    this.perf = getPerformance(this.app)
  }

  async logEvent(event: string, o: any) {
    if (!this.analytics) {
      return
    }
    logEvent(this.analytics, event, o)
  }

  async getValueFromConfig(key: string): Promise<string | null> {
    if (this.remoteConfig) {
      try {
        await fetchAndActivate(this.remoteConfig)
        const val = getValue(this.remoteConfig, key)
        return val.asString()
      } catch (e) {
        return null
      }
    }
    return null
  }
}

export const firebaseManager = FirebaseManager.getInstance()
