enum FriendStatus {
  AVAILABLE = 0,
  ALREADY_INVITE = 1,
  CAN_NOT_INVITE = 2,
}

export interface FriendItem {
  status: FriendStatus
  display_uid: number
  nickname: string
  icon: string
  uid: number
}

export interface inviteListRes {
  items: FriendItem[]
}

enum InvitationStatus {
  CAN_JOIN = 0,
  ALREADY_JOIN = 1,
  FULL = 2,
  RUN_AWAY = 3,
  EXPIRE = 4,
}

interface InviteItem {
  uid: number
  status: InvitationStatus
  display_uid: number
  nickname: string
  icon: string
}

export interface inviterecRes {
  items: InviteItem[]
}

interface MemberItem {
  uid: number
  score: number
  icon: string
}

export interface teamRes {
  name: string
  hasNewInvite: boolean
  isLeader: boolean
  members: MemberItem
}

interface RewardInfo {
  icon: string
  name: string
  cn_name: string
  expire: number
  num: number
  type: number
  reward_id: number
  price: number
}

export interface lotteryRes {
  data: RewardInfo[]
  is_clear: boolean
}

interface RewardRecordInfo {
  rewards: RewardInfo[]
  key: number
  is_all: boolean
  time: number
}

export interface recordRes {
  data: RewardInfo[]
}

